@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;800&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

:root {
    --primaryColor: #DE809D;
    --hoverColor: #FEC7C7;
    --loginBtn: #190152;
    --whiteColor: #ffffff;
    --blackColor: hsl(0, 0%, 18%);
    --greyText: #636363;
    --bgColor: #FFECD0;
    --inputColor: hsl(330, 12%, 97%);

    --biggestFontSize: 2.5rem;
    --h1FontSize: 1.5rem;
    --h2FontSize: 1.25rem;
    --h3FontSize: 1rem;
    --normalFontSize: 0.938rem;
    --smallFontSize: 0.813rem;
    --smallestFontSize: 0.75rem;
}

.flex {
    display: flex;
    align-items: center;
}

.grid {
    display: grid;
    align-items: center;
    gap: 1.5rem;
}

html {
    font-size: 90%;
}

a {
    text-decoration: none;
}

li {
    list-style: none;
}

.icon {
    font-size: var(--h2FontSize);
    cursor: pointer;
}

img,
video {
    height: auto;
    width: 100%;
}

.btn {
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 0.5rem;
    padding: 0.5rem 1rem;
    color: var(--greyText);

    .icon {
        margin-left: 0.313rem;
    }

    &:hover {
        color: var(--primaryColor);
        background: var(--paleGreen);
    }
}